import { useState, useCallback } from "react";
import {
  FormControlLabel,
  Radio,
  Autocomplete,
  TextField,
  Icon,
} from "@mui/material";
import { LocalizationProvider, DatePicker } from "@mui/x-date-pickers";
import { AdapterDateFns } from "@mui/x-date-pickers/AdapterDateFns";
import { useNavigate } from "react-router-dom";
import styles from "./SearchContainer.module.css";
const SearchContainer = () => {
  const [
    selectOutlinedDateTimePickerValue,
    setSelectOutlinedDateTimePickerValue,
  ] = useState(null);
  const navigate = useNavigate();

  const onSearchFlightsButtonClick = useCallback(() => {
    navigate("/results-page");
  }, [navigate]);

  const [flightType, setFlightType] = useState("return");

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns}>
      <div className={styles.searchSection}>
        <div className={styles.bannerGradient} />
        <img
          className={styles.bannerBackground}
          alt=""
          src="/banner--background@2x.png"
        />
        <div className={styles.searchContainer}>
          <div className={styles.title}>
            <div
              className={styles.letsExplore}
            >{`Lets explore & travel the world`}</div>
            <div className={styles.findTheBest}>
              Find the best destinations and the most popular stays!
            </div>
          </div>
          <div className={styles.searchForm}>
            <div className={styles.formTitleGroup}>
              <b className={styles.searchFlights}>Search flights</b>
              <div className={styles.flightOptions}>
                <div className={styles.flightType}>
                  <FormControlLabel
                    className={styles.radio}
                    label="Return"
                    labelPlacement="end"
                    control={<Radio size="medium" checked={flightType === 'return'} value='return' onChange={(e)=>setFlightType(e.target.value)}/>}
                  />
                  <FormControlLabel
                    className={styles.radio1}
                    label="One-way"
                    labelPlacement="end"
                    control={<Radio color="primary" checked={flightType === 'one-way'} value='one-way' onChange={(e)=>setFlightType(e.target.value)} size="medium" />}
                  />
                </div>
              </div>
            </div>
            <div className={styles.formInputsRow}>
              <div className={styles.inputsRow}>
                <Autocomplete
                  className={styles.departureField}
                  disablePortal
                  options={[
                    "Singapore (SIN)",
                    "Sydney (SYD)",
                    "Siem Reap (REP)",
                    "Shanghai (PVG)",
                    "Sanya (SYX)",
                  ]}
                  renderInput={(params) => (
                    <TextField
                      {...params}
                      color="primary"
                      label="Departure"
                      variant="outlined"
                      placeholder=""
                      helperText="Singapore (SIN)"
                    />
                  )}
                  defaultValue="Singapore -  Changi (SIN)"
                  size="medium"
                />
                  <Autocomplete
                    className={styles.departureField}
                    sx={{ width: "100%" }}
                    disablePortal
                    options={[
                      "Clark (CRK)",
                      "Launceston (LST)",
                      "Kalibo (KLO)",
                      "Colombo CMB",
                      "Melbourne (AVV)",
                      "Los Angeles (LA)",
                    ]}
                    renderInput={(params) => (
                      <TextField
                        {...params}
                        color="primary"
                        label="Arrival"
                        variant="outlined"
                        placeholder=""
                        helperText="Los Angeles (LA)"
                      />
                    )}
                    defaultValue="Los Angeles (LA)"
                    size="medium"
                  />
                <div className={styles.departureField}>
                  <DatePicker
                    label="Date"
                    value={selectOutlinedDateTimePickerValue}
                    onChange={(newValue) => {
                      setSelectOutlinedDateTimePickerValue(newValue);
                    }}
                    renderInput={(params) => (
                      <TextField
                        {...params}
                        color="primary"
                        variant="outlined"
                        size="medium"
                        renderInput={{ placeholder: "01/05/2022" }}
                        helperText=""
                        fullWidth
                      />
                    )}
                  />
                </div>
              </div>
              <div className={styles.buttonGroup}>
                <div
                  className={styles.searchFlightsButton}
                  onClick={onSearchFlightsButtonClick}
                >
                  <div className={styles.button}>Search flights</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </LocalizationProvider>
  );
};

export default SearchContainer;
